import React, { Component } from 'react';
import {
  XYPlot,
  XAxis,
  YAxis,
  HorizontalGridLines,
  VerticalGridLines,
  LineSeries
} from 'react-vis';
import * as moment from 'moment';

class DataGraph extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: props.name,
      units: props.units,
      dataset: []
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.dataset !== prevState.dataset) {
      let dataset = nextProps.dataset.map((record) => {
        return {
          x: moment(record.rt).toDate(),
          y: record.sv
        };
      });
      dataset.sort((d1, d2) => {
      	return d1.getTime() > d2.getTime();
      });
      return {
        dataset: dataset
      }
    }
    return null;
  }

  render() {
    return (
      <div className="card">
        <div className="card-body">
          <h5 className="card-title">{this.state.name}</h5>
          <XYPlot width={424} height={300}>
            <HorizontalGridLines />
            <VerticalGridLines />
            <XAxis title="Time" />
            <YAxis title={this.state.units} />
            <LineSeries
              color={this.props.color}
              data={this.state.dataset}/>
          </XYPlot>
        </div>
      </div>
    )
  }
}

export default DataGraph;



