import React, { Component } from 'react';
import './App.css';
import DataGraph from './DataGraph';
import * as moment from 'moment';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      model: []
    };
  }

  componentDidMount() {
    const taskId = setInterval(this.fetchData, 5000);
  }

  fetchData = () => {
    let queryAt = moment().subtract(1, 'hours');
    //http://localhost:3027/
    fetch('/data?filter=' + queryAt.format())
      .then((result) => {
        result.json().then((response) => {
          this.setState({
            model: response
          });
        });
    });
  }

  filterModel(select) {
    return this.state.model
      .filter((sample) => {
        return sample.sn === select;
      });
  }

  render() {
    return (
      <div className="App">
        <nav className="navbar navbar-dark tskr-navbar">
          <a className="navbar-brand" href="https://tcat.cs.washington.edu/">
            Taskar Data Collector
          </a>
        </nav>
        <div className="container-fluid">
          <div className="row tskr-row">
            <div className="col-md-4">
              <DataGraph name="Sound" units="10-bit ADC"
                dataset={this.filterModel("sound")}
                color="cyan"/>
            </div>
            <div className="col-md-4">
              <DataGraph name="UV" units="UV Index"
                dataset={this.filterModel("UV")}
                color="purple"/>
            </div>
            <div className="col-md-4">
              <DataGraph name="Equivalent-CO2" units="ppm"
                dataset={this.filterModel("eCO2")}
                color="pink"/>
            </div>
          </div>
          <div className="row tskr-row">
            <div className="col-md-4">
              <DataGraph name="Temperature" units="celsius"
                dataset={this.filterModel("temp")}
                color="green"/>
            </div>
            <div className="col-md-4">
              <DataGraph name="Infrared" units="adc"
                dataset={this.filterModel("IR")}
                color="red"/>
            </div>
            <div className="col-md-4">
              <DataGraph name="VOC" units="ppb"
                dataset={this.filterModel("VOC")}
                color="maroon"/>
            </div>
          </div>
          <div className="row tskr-row">
            <div className="col-md-4">
              <DataGraph name="Humidity" units="percent"
                dataset={this.filterModel("humid")}
                color="navy"/>
            </div>
            <div className="col-md-4">
              <DataGraph name="Visible Light" units="adc"
                dataset={this.filterModel("visible")}
                color="yellow"/>
            </div>
            <div className="col-md-4">
              <DataGraph name="Hall Sensor" units="digital"
                dataset={this.filterModel("hall")}
                color="black"/>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



